<template>
  <div class="analytics" v-show="showConsentModal">
    <div class="analytics_item">
      <p class="analytics_msg">
        {{ $t("analytics.cookie_desc") }}
        <a
          href="https://dijets.io/privacy-policy"
          target="_blank"
          rel="noopener noreferrer"
        >
          {{ $t("analytics.privacy_policy") }}
        </a>
        .
      </p>
      <div class="analytics_actions">
        <button
          type="button"
          @click="onReject"
          data-cy="reject_analytics"
          class="analysis_button"
        >
          <svg
            width="12px"
            height="12px"
            viewBox="0 0 32 32"
            xmlns="http://www.w3.org/2000/svg"
            fill="currentColor"
          >
            <path
              class="cls-1"
              d="M28,29a1,1,0,0,1-.71-.29l-24-24A1,1,0,0,1,4.71,3.29l24,24a1,1,0,0,1,0,1.42A1,1,0,0,1,28,29Z"
            />

            <path
              class="cls-1"
              d="M4,29a1,1,0,0,1-.71-.29,1,1,0,0,1,0-1.42l24-24a1,1,0,1,1,1.42,1.42l-24,24A1,1,0,0,1,4,29Z"
            />
          </svg>
          <span>Reject</span>
        </button>
        <button
          type="button"
          @click="onAccept"
          data-cy="accept_analytics"
          class="analysis_button"
        >
          <svg
            width="12px"
            height="12px"
            viewBox="0 0 32 32"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              class="cls-1"
              d="M12.16,28a3,3,0,0,1-2.35-1.13L3.22,18.62a1,1,0,0,1,1.56-1.24l6.59,8.24A1,1,0,0,0,13,25.56L27.17,4.44a1,1,0,1,1,1.66,1.12L14.67,26.67A3,3,0,0,1,12.29,28Z"
            />
          </svg>
          <span>Accept</span>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // if the user never accepted or rejected, we show the prompt
      showConsentModal: localStorage.getItem("consentsToAnalytics") === null,
    };
  },
  computed: {},
  methods: {
    onAccept() {
      localStorage.setItem("consentsToAnalytics", true);
      // @ts-ignore
      this.showConsentModal = false;

      return;
    },
    onReject() {
      localStorage.setItem("consentsToAnalytics", false);
      // @ts-ignore
      this.showConsentModal = false;

      return;
    },
  },
};
</script>
